<template>
  <div id="tvseries">
    <div id="header">
        <nav>
          <router-link :to="{ name: 'SearchTvSeries', params: {currentUser: this.user }}">Search</router-link>
          <router-link :to="{ name: 'OnWatch', params: {currentUser: this.user }}">On Watch</router-link>
          <router-link :to="{ name: 'WatchList', params: {currentUser: this.user }}">Watch List</router-link>
        </nav>
    </div>
    <div id="main" v-if="isReady">
      <router-view></router-view>
    </div>
  </div>
</template>

<script>
import userService from '../../../services/userServiceTmdb'
import router from '../../router'
export default {
  name: 'TvSeriesPage',
  props: ["currentUser"],
  data () {
    return {
      user: this.currentUser,
      isReady: false
    }
  },

  mounted(){
    this.isReady = true
    router.push({ name: 'SearchTvSeries', params: {currentUser: this.user }})
  },

  methods: {

  }
}
</script>

<style>
</style>



