<template>
    <div id="searchtvseries">
        <div class="row justify-content-md-center">
                <div class="col col-lg-2"><input type="text" placeholder="Search Title.."  v-model="title"></div>
                <div class="col col-lg-2"><button v-on:click="searchTvSeries">Search</button></div>
        </div>
        <div class="row justify-content-md-center"  v-for="result in searchResults">
                <div class="col col-lg-10" v-show="isSearchEnded">
                  <b><p><u>"{{result.title}}"</u> ({{result.airDate}} - {{result.country}})</p></b>
                  <p>{{result.overview}}</p>
                </div>
                <div class="col col-lg-2"><button v-on:click="addTvSeries(result.tmdbID)">Add</button></div>
        </div>
    </div>
</template>

<script>
import tmdbService from '../../../services/tmdbService'
import userServiceTmdb from '../../../services/userServiceTmdb'
import userServiceTvSeries from '../../../services/userServiceTvSeries'
import router from '../../router'
export default {
  name: "SearchTvSeries",
  props: ['currentUser'],
  data () {
    return {
      user: this.currentUser,
      title: null,
      searchResults: [],
      isSearchEnded: false
    }
  },


  methods: {

    async searchTvSeries () {   
      let response
      try{
        response = await tmdbService.searchTvSeries(this.title)
      }
      catch (err){
        response = err.response
      }
      finally {

        if (response.status === 200){

          this.searchResults = []
          for (let i = 0; i < response.data.results.length; i++){
            let result = {
              tmdbID: response.data.results[i].id,
              title: response.data.results[i].original_name,
              airDate: response.data.results[i].first_air_date.split('-')[0],
              country: response.data.results[i].origin_country.join(),
              overview: response.data.results[i].overview
            }
            this.searchResults.push(result)
            this.isSearchEnded= true
            this.title = null
          }
        }
      }
    },

    async addTvSeries(tmdbID){
      let response
      try{
        response = await userServiceTmdb.addTvSeries(tmdbID)
      }
      catch (err){
        error = err.response
      }
      finally {
        if (response.status == 200 || response.status == 201){
          let userResponse
          try{
            userResponse = await userServiceTvSeries.addTvSeries(this.user, tmdbID)
          }
          catch (err){
            userResponse = err.response
            console.log( userResponse.data.message)
          }
          finally {
              if (userResponse.status == 200){
                router.push({ name: 'WatchList', params: {currentUser: this.user }})
              }
          }      
        }
      }
    }

  }
}
</script>

<style>

</style>



